<template>

    <div>
        <el-form :inline="true" :model="query" class="query-form" size="mini">
            <el-form-item class="query-form-item">
                <el-input v-model="query.taskName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button-group>
                    <el-button type="primary" icon="el-icon-refresh" @click="onReset"></el-button>
                    <el-button type="primary" icon="search" @click="onSubmit"></el-button>
                    <el-button type="primary" @click.native="handleForm(null,null)"></el-button>
                </el-button-group>
            </el-form-item>
        </el-form>
        <el-table
            v-loading="loading"
            :data="list"
            style="width: 100%;">
            <el-table-column
                label=""
                prop="id"
                width="150"
                fixed>
            </el-table-column>
            <el-table-column
                label=""
                prop="taskName"
                with="300"
                :show-overflow-tooltip="true"
                fixed>
            </el-table-column>
            <el-table-column
                label=""
                prop="sourceDatabaseName"
                with="300"
                :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                label=""
                prop="targetDatabaseName">
            </el-table-column>
            <el-table-column
                label=""
                prop="taskStatusDesc"
                with="300"
                :show-overflow-tooltip="true">
                <template slot-scope="scope">
                    <el-tag
                        :type="scope.row.taskStatusDesc === '' ? 'info' :
                               scope.row.taskStatusDesc === '' ? 'warning' :
                               scope.row.taskStatusDesc === '' ? 'success' :
                               scope.row.taskStatusDesc === '' ? 'danger' : 'default'"
                        disable-transitions>{{scope.row.taskStatusDesc}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column
                label=""
                prop="startTime"
                with="300"
                :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                label=""
                prop="endTime"
                with="300"
                :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                label=""
                fixed="right">
                <template slot-scope="scope">
                    <el-button type="text" size="small" v-if="scope.row.taskStatus == 0 || scope.row.taskStatus ==3" @click.native="handleStart(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" v-if="scope.row.taskStatus == 2" @click.native="handleViewDetail(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click.native="handleForm(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click.native="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
            :page-size="query.limit"
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :total="total">
        </el-pagination>

        <!---->
        <el-dialog
            title=""
            :visible.sync="formVisible"
            :before-close="hideForm"
            style="--el-dialog-margin-top: 2%; --el-dialog-width: 50%;">
            <el-form :model="formData" :rules="formRules" ref="dataForm">
                <el-form-item label="" prop="taskName"  label-width="140px">
                    <el-input v-model="formData.taskName" placeholder="" auto-complete="off" clearable="true"></el-input>
                </el-form-item>
                        <el-form-item label="" prop="sourceDatabaseName"  label-width="140px">
                            <el-select v-model="formData.sourceId"  auto-complete="off" filterable  placeholder="">
                                <el-option
                                    v-for="option in sourceDataBaseOptionsList"
                                    :key="option.value"
                                    :label="option.label"
                                    :value="option.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                <el-form-item label="" prop="targetDatabaseName"  label-width="140px">
                    <el-select v-model="formData.targetId"  auto-complete="off" filterable  placeholder="">
                        <el-option
                            v-for="option in targetDataBaseOptionsList"
                            :key="option.value"
                            :label="option.label"
                            :value="option.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="hideForm"></el-button>
                <el-button type="primary" @click.native="formSubmit()" :loading="formLoading"></el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="taskVisible" width="80%">
            <el-form :inline="true"  :model="searchQuery" class="query-form" size="mini">
                <el-form-item label="">
                    <el-input v-model="searchQuery.taskDetail" placeholder="" clearable="true"></el-input>

                </el-form-item>
                <el-form-item label="">
                <el-input v-model="searchQuery.taskType" placeholder="" clearable="true"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="taskSearch"></el-button>
                </el-form-item>
            </el-form>
            <el-table :data="filteredTaskDetails" style="width: 100%">
                <el-table-column type="index" label="" width="50px"></el-table-column>
                <el-table-column prop="taskType" label="" width="180px" ></el-table-column>
                <el-table-column prop="taskDetail" label=""></el-table-column>
                <el-table-column prop="createTime" label="" width="180px"></el-table-column>
            </el-table>
            <el-pagination
                @current-change="taskCurrentChange"
                :current-page="searchQuery.page "
                :page-size="searchQuery.limit"
                layout="prev, pager, next"
                :total="taskDetailTotal">
            </el-pagination>
        </el-dialog>
    </div>

</template>

<script>
import { taskList, taskSave, taskDelete,sourceDataBaseList,targetDataBaseList,taskStart,viewResult } from "../../api/task/task";
import Upload from "../../components/File/Upload.vue";
const formJson = {
    id: "",
    taskName: "",
    sourceId: "",
    sourceDatabaseName: "",
    targetDatabaseName: "",
    targetId: "",
    taskStatus: "",
    taskStatusDesc: ""
};
export default {
    props: {
        taskDetails: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            query: {
                taskName: "",
                page: 1,
                limit: 20
            },
            searchQuery:{
                taskDetail: "",
                taskType: "",
                page: 1,
                limit: 20,
                taskId: ""
            },
            filteredTaskDetails:[],
            taskDetailTotal : 0,
            sourceDataBaseOptionsList: [],
            targetDataBaseOptionsList: [],
            list: [],
            total: 0,
            loading: true,
            index: null,
            formName: null,
            formMap: {
                add: "",
                edit: ""
            },
            formLoading: false,
            formVisible: false,
            taskVisible: false,
            formData: formJson,
            formRules: {
                connectName: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                databaseType: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ]
            },
            deleteLoading: false
        };
    },
    components: {
        Upload
    },
    created() {
        this.sourceDataBaseOptions();
        this.targetDataBaseOptions();
        // 
        let query = this.$route.query;
        this.query = Object.assign(this.query, query);
        this.query.limit = parseInt(this.query.limit);
        // 
        this.getList();
    },
    methods: {
        sourceDataBaseOptions(){
            sourceDataBaseList().then(res => {
                this.sourceDataBaseOptionsList = res.data;
            })
        },
        targetDataBaseOptions(){
            targetDataBaseList().then(res => {
                this.targetDataBaseOptionsList = res.data;
            })
        },
        onReset() {
            this.$router.push({
                path: ""
            });
            this.query = {
                connectName: "",
                page: 1,
                limit: 20
            };
            this.getList();
        },
        onSubmit() {
            this.$router.push({
                path: "",
                query: this.query
            });
            this.getList();
        },
        handleCurrentChange(val) {
            this.query.page = val;
            this.getList();
        },
        getList() {
            this.loading = true;
            taskList(this.query)
                .then(response => {
                    this.loading = false;
                    this.list = response.data.list || [];
                    this.total = response.data.total || 0;
                })
                .catch(() => {
                    this.loading = false;
                    this.list = [];
                    this.total = 0;
                });
        },
        // 
        resetForm() {
            if (this.$refs["dataForm"]) {
                // 
                this.$refs["dataForm"].resetFields();
                // 
                this.$refs["dataForm"].clearValidate();
            }
        },
        // 
        hideForm() {
            // 
            this.formVisible = !this.formVisible;
            return true;
        },
        // 
        handleForm(index, row) {
            this.formVisible = true;
            // 
            this.resetForm();
            this.formData = JSON.parse(JSON.stringify(formJson));
            if (row !== null) {
                this.formData = Object.assign({}, row);
            }
            this.formName = "add";
            if (index !== null) {
                this.index = index;
                this.formName = "edit";
            }
        },
        handleStart(index, row){
            taskStart(row).then(res => {
                if (res.code==0) {
                    this.$message.success('');
                    this.onSubmit();
                }
            }).catch(err => {
                this.$message.error('');
            });
        },
        taskCurrentChange(val) {
            this.searchQuery.page = val;
            this.getTaskDetailList();
        },
        taskSearch(){
          this.searchQuery.page = 1;
            this.getTaskDetailList();
        },

        getTaskDetailList(){
            viewResult(this.searchQuery).then(res => {
                if (res.code==0) {
                    this.filteredTaskDetails = res.data.list || [];
                    this.taskDetailTotal = res.data.total || 0;
                }
            }).catch(err => {
                this.$message.error('');
            });
        },

        handleViewDetail(index, row){
            this.taskVisible = true;
            this.searchQuery.page = 1;
            this.searchQuery.taskDetail = "";
            this.searchQuery.taskType = "";
            this.searchQuery.taskId = row.id;
            viewResult(this.searchQuery).then(res => {
                if (res.code==0) {
                    this.filteredTaskDetails = res.data.list || [];
                    this.taskDetailTotal = res.data.total || 0;
                }
            }).catch(err => {

            });
        },
        formSubmit() {
            this.$refs["dataForm"].validate(valid => {
                if (valid) {
                    this.formLoading = true;
                    let data = Object.assign({}, this.formData);
                    taskSave(data, this.formName)
                        .then(response => {
                            this.formLoading = false;
                            if (response.code) {
                                this.$message.error(response.message);
                                return false;
                            }
                            this.$message.success("");
                            this.formVisible = false;
                            if (this.formName === "add") {
                                // 
                                data.id = response.data.id;
                                this.list.unshift(data);
                            } else {
                                this.list.splice(this.index, 1, data);
                            }
                            this.onSubmit();
                        })
                        .catch(() => {
                            this.formLoading = false;
                        });
                }
            });
        },
        // 
        handleDel(index, row) {
            if (row.id) {
                this.$confirm("?", "", {
                    type: "warning"
                })
                    .then(() => {
                        let para = { id: row.id };
                        this.deleteLoading = true;
                        taskDelete(para)
                            .then(response => {
                                this.deleteLoading = false;
                                if (response.code) {
                                    this.$message.error(response.message);
                                    return false;
                                }
                                this.$message("");
                                // 
                                this.list.splice(index, 1);
                            })
                            .catch(() => {
                                this.deleteLoading = false;
                            });
                    })
                    .catch(() => {
                        this.$message.info("");
                    });
            }
        },


    },
    filters: {
        statusFilterType(status) {
            const statusMap = {
                0: "gray",
                1: "success"
            };
            return statusMap[status];
        },
        statusFilterName(status) {
            const statusMap = {
                0: "",
                1: ""
            };
            return statusMap[status];
        }
    },
    mounted() {}
};
</script>

<style type="text/scss" lang="scss">
</style>
